body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}

.app{
  background-color: #111;
}
.row-posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    
}

.row{
    margin-left: 20px;
}

.row-posters::-webkit-scrollbar{
    display: none;
}

.row-poster{
    object-fit: contain;
    max-height: 100px;
    width : 100%;
    margin-right: 10px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
}

.row-poster:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}
.row-posterLarge{
    max-height: 250px;
}

.row-posterLarge:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
}

.title{
    padding-top: 20px;
    color: whitesmoke;
}
.banner{
    height: 448px;
    width: 100%;
    background-image: url(/Web-practice/netflix-clone/netfix-clone/static/media/banner.236beedc.png);
    background-size: cover;
    
}
.nav{
    width: 100%;
    position: fixed;
    padding: 20px;
    height: 30px;
    margin: 0;
    background-color: black;
    z-index: 1;
}

.nav__logo{
    width: 80px;
    object-fit: contain;
}
